.nav_bar {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 4.5rem;
  justify-content: space-between;
  padding: 1em;
  position: fixed;
  top: 0;
  background-color: white;
  border-bottom: 1px solid #e3e3e3;
  z-index: 1000;

  img {
    width: 50px;
  }
}
.sidebar_top {
  display: flex;
  justify-content: center;
  img {
    width: 50px;
  }
}

.nav_barMobile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
  width: 100%;
  height: 8.5em;
  padding: 1em;
  position: fixed;
  top: 0;
  background-color: white;
  border-bottom: 1px solid #e3e3e3;
  z-index: 1000;
}
.rightWrapper {
  display: flex;
}

.nav_bar h1 {
  font-size: 2rem;
}
.nav_barMobile h1 {
  font-size: 2rem;
}
@media (max-width: 767px) {
  .sidebar_btn {
    cursor: pointer;
    div {
      margin-right: 8px;
    }
  }
  .dropList {
    top: 136px;
  }
  .sidebar_bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 10px;
  }
  .test {
    width: 100%;
  }
}
